@import url("http://www.openhiun.com/hangul/nanumbarungothic.css");
@import url("https://fonts.googleapis.com/css2?family=Do+Hyeon&display=swap");

@media (max-width: 600px) {
  .button-container-review {
    display: flex;
    justify-content: space-between;
  }
  #review-upload {
    border-radius: 10px;
    width: 44%;
    height: 29px;
    float: right;
    border: solid 2px rgba(160, 160, 160, 0.514);
    background-color: rgba(160, 160, 160, 0.214);
    font-family: "Nanum Barun Gothic", sans-serif;
    font-size: 11px;
  }

  #review-upload-cancel {
    border-radius: 10px;
    width: 100%;
    height: 29px;
    float: right;
    border: solid 2px rgba(160, 160, 160, 0.514);
    background-color: rgba(160, 160, 160, 0.214);
    font-family: "Nanum Barun Gothic", sans-serif;
    font-size: 11px;
  }
  .dv {
    padding-top: 10%;
    padding-bottom: 10%;
    height: 100%;
    padding-left: 5%;
    padding-right: 5%;
  }

  .review-wrap {
    height: 90%;
  }

  .header-title {
    font-size: 15px;
  }

  .review-title {
    padding-top: 10%;
    width: 100%;
  }
}

@media (min-width: 601px) {
  .dv {
    padding-top: 7%;
    padding-bottom: 3%;
    height: 100%;
  }
  .review-wrap {
    width: 80%;
    height: 90%;

    margin-left: 10%;
  }

  .review-title {
    width: 100%;
  }
}

/* review-title */
.button-container-review {
  display: flex;
  justify-content: space-between;
}
#review-upload {
  border-radius: 10px;
  width: 30%;
  height: 29px;
  margin-bottom: 10px;
  float: right;
  border: solid 2px rgba(160, 160, 160, 0.514);
  background-color: rgba(160, 160, 160, 0.214);
  font-family: "Nanum Barun Gothic", sans-serif;
  font-size: 11px;
}

#review-upload-cancel {
  border-radius: 10px;
  width: 100%;
  height: 29px;
  float: right;
  border: solid 2px rgba(160, 160, 160, 0.514);
  background-color: rgba(160, 160, 160, 0.214);
  font-family: "Nanum Barun Gothic", sans-serif;
  font-size: 11px;
}
#title {
  height: 50px;
  border: solid 2.3px rgba(160, 160, 160, 0.514);
  font-family: "Do Hyeon", sans-serif;
  font-size: 24px;
  color: rgb(53, 53, 53);
  padding-top: 5px;
  padding-left: 10px;
}

/* search-button-group */
