}
#review-search-typeName {
  padding-left: 5px;
  font-family: "Nanum Barun Gothic", sans-serif;
  font-size: 12px;
  width: 26%;
  height: 40px;
  background: 0 0;
  border: 1px solid #d8dade;
  box-sizing: border-box;
  border-radius: 8px;
  color: #363b44;
  z-index: 1;
  cursor: pointer;
  font-size: 14px;

  align-items: center;
}
#b {
  font-weight: 950;
  font-size: 12px;
}

#review-search {
  width: 250px;
  height: 29px;
  margin-left: 10px;
  border: solid 2px rgba(160, 160, 160, 0.514);

  font-family: "Nanum Barun Gothic", sans-serif;
  font-size: 12px;
}

#merit,
#demerit {
  margin-top: 10px;
}

/* reviews */
.review-container {
  width: 100%;
  margin-top: 1.3em;
  height: 100%;
  display: flex;
  flex-direction: row;
}
.create-review-container {
  margin-top: 1.3em;
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
}

.review-block {
  margin: 0px 5px;
  flex-basis: 49%;
  border: solid 1px black;
  box-shadow: 0px 7px 16px 0px rgba(0, 0, 0, 0.1);
  padding: 20px 35px;
  border-radius: 3px;
  font-family: "Nanum Barun Gothic", sans-serif;
}

#header {
  margin-bottom: 0.3em;
  text-align: center;
  font-family: "Do Hyeon", sans-serif;
  font-size: 15px;
}

#writer-writeDate-star {
  font-family: "Nanum Barun Gothic", sans-serif;
  font-size: 13px;
  float: right;
  margin-bottom: 0.3em;
}

.houseInfo {
  width: 100%;
  font-family: "Nanum Barun Gothic", sans-serif;
  font-size: 13px;
  text-align: center;
}

.houseInfo td {
  border: solid 1px grey;
}

#a {
  background-color: rgba(59, 89, 114, 0.288);
}

.review-image {
  width: 100%;
  float: center;
  height: auto;
}

#reviewImage {
  margin-top: 1em;
  width: 100%;
  height: 270px;
  border: solid 0.5px rgb(173, 173, 173);
}

.review-content {
  margin-top: 10px;
  font-family: "Nanum Barun Gothic", sans-serif;
}

#merit,
#demerit {
  font-size: 12.5px;
}

#center {
  margin-top: 2em;
  width: 100%;
}

.pagination {
  justify-content: center;
  display: flex;
}

.pagination a:hover:not(.active) {
  background-color: #ddd;
}

.pagination a {
  color: black;
  float: left;
  padding: 8px 16px;
  text-decoration: none;
  transition: background-color 0.3s;
  border: 1px solid #ddd;
  margin: 0 4px;
}

.pagination a.active {
  background-color: #4c87afa2;
  color: white;
  border: 1px solid #4c87afa2;
}

select.sorting {
  width: 50%;
  height: 40px;
  background: 0 0;
  border: 1px solid #d8dade;
  box-sizing: border-box;
  border-radius: 8px;
  color: #363b44;
  z-index: 1;
  cursor: pointer;
  font-size: 14px;
  display: flex;
  align-items: center;
  padding: 0 16px;
  position: relative;
}

.review-item {
