.review-item-description {
  margin-top: 16px;
  word-break: break-word;
  font-size: 15px;
  line-height: 22px;
  letter-spacing: -0.03em;
  color: #5f6877;
}
.review-item-description-header {
  font-size: 16px;
  line-height: 28px;
  color: #111;
  font-weight: 950;
  text-align: center;
}
.review-item-description-date {
  font-size: 16px;
  line-height: 28px;
  color: #111;
}
.review-item-description-title {
  font-size: 16px;
  line-height: 28px;
  color: #111;
  font-weight: 950;
}
.review-veil {
  width: 100%;
  height: 51px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  background: #f7f7f9;
  border-radius: 8px;
  font-size: 13px;
  line-height: 19px;
  letter-spacing: -0.03em;
  padding: 12px;
  box-sizing: border-box;
}

span.starpoint img {
  width: 19px;
  height: 17px;
  margin: 0 4px 0 0;
}
.bldg-img {
  width: 72px;
  height: 72px;
  display: inline-block;
  margin-right: 17px;
  background-size: cover;
}

.status {
  float: right;

  margin-left: 8px;
  padding: 0 2px;
  height: 20px;
  line-height: 20px;
  color: #a6a6a6;
  font-size: 12px;
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 11px 11px;
  color: #363b44;
  z-index: 1;
  cursor: pointer;
}
.user {
  float: none;
  max-width: 90px;
  margin: 0 10px 5px 0;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  font-weight: bold;
  overflow: hidden;
  text-overflow: ellipsis;
}
.time {
  float: none;
  margin-bottom: 5px;
  height: 20px;
  line-height: 20px;
  font-size: 12px;
  color: #a6a6a6;
}

.picture-medium {
  float: left;
  margin: 0 5px 5px 0;
  width: 20px;
  height: 20px;
  border-radius: 4px;
}

@import url(//netdna.bootstrapcdn.com/font-awesome/3.2.1/css/font-awesome.css);
fieldset,
label {
  margin: 0;
  padding: 0;
}

h1 {
